import { useState, useEffect } from "react";
import { ToastContainer } from "react-toastify";

import {
  deleteAll,
  deleteItem,
  getHistory,
  initStorage,
  saveHistory,
} from "./utils/storage";
import SearchHistory from "./components/SearchHistory/searchHistory";
import SearchPanel from "./components/searchPanel";

import "./App.css";

import DeleteButton from "./components/DeleteButton/deleteButton";

function App() {
  //State and Setter for Search history
  const [historyList, setHistoryList] = useState(getHistory());
  //Delete function for removing a item from search history
  const handleDeleteHistoryItem = (item) => {
    deleteItem(item).then(setHistoryList(getHistory()));
  };

  //Add new Item in Search history then get new history
  const handleAddNewHistoryItem = (item) => {
    saveHistory(item);
    setHistoryList(getHistory());
  };

  const handleDeleteAllHistory = () => {
    deleteAll().then(setHistoryList(getHistory()));
  };

  useEffect(() => {
    initStorage();
  }, []);

  return (
    <div className="App">
      <SearchPanel
        onAddHistoryItem={handleAddNewHistoryItem}
        onDeleteHistoryItem={handleDeleteHistoryItem}
      />
      <SearchHistory
        history={historyList}
        onDeleteHistoryItem={handleDeleteHistoryItem}
        onDeleteAll={handleDeleteAllHistory}
      />
      {/* <SearchHistory
        history={historyList}
        onDeleteHistoryItem={handleDeleteHistoryItem}
      /> */}
      <ToastContainer theme="colored" position="bottom-right" />
    </div>
  );
}

export default App;
